<template>
  <div style="margin-top: 40px">
    <div class="scenarios-area">
     <el-row>
        <el-col
          v-for="scenario in scenarios"
          :key="scenario.id"
          :span="6"
        >
          <el-card :span="8" :body-style="{ padding: '0px' }" class="box-card"  shadow="hover">
            <div class = "head">
             <router-link class="scenario-link" :to="{path:'library/scenarios',query:{id: scenario.id}}"> <li><span style="font-size: 20px"><strong>{{scenario.name}}</strong></span></li></router-link>
            </div>
            <video controls width="210" :src="scenario.video" type="video/mp4"></video>
          </el-card>
        </el-col>
      </el-row>
    </div>
    <el-pagination
      background
      layout="total, prev, pager, next, jumper"
      @current-change="handleCurrentChange"
      :page-size="pageSize"
      :total="total">
    </el-pagination>
  </div>
</template>

<script>

  export default {
      name: 'Scenarios',
      data () {
        return {
          scenarios: [],
          pageSize: 12,
          total: 0,
          r: 0, // 用于判断是否是筛选后的分页
          did: 0,
          tid: 0,
          oid: 0,
          nid: 0
          }
        },
      mounted: function () {
        this.loadScenarios()
      },
      methods: {
        loadScenarios () {
          var _this = this
          this.$axios.get('/scenarios/' + this.pageSize + '/1').then(resp => {
            if (resp && resp.data.code === 200) {
              _this.scenarios = resp.data.result.content
              _this.total = resp.data.result.totalElements
            }
          })
        },
        handleCurrentChange (page) {
          var _this = this
          if (_this.r === 0) {
            this.$axios.get('/scenarios/' + this.pageSize + '/' + page).then(resp => {
            if (resp && resp.data.code === 200) {
              _this.scenarios = resp.data.result.content
              _this.total = resp.data.result.totalElements
              }
            })
          } else {
            var url = 'filter/' + this.did + '/' + this.tid + '/' + this.oid + '/' + this.nid + '/' + this.pageSize + '/' + page + '/scenarios'
            this.$axios.get(url).then(resp => {
            if (resp && resp.data.code === 200) {
              _this.scenarios = resp.data.result.content
              _this.total = resp.data.result.totalElements
              }
            })
          }
        }
      }
    }
</script>

<style scoped>
.scenarios-area {
  width: 1200px;
  height: 700px;
  margin-left: auto;
  margin-right: auto;
}
.scenario-link {
  text-decoration: none;
  color: #606266;
}
.scenario-link:hover {
  color: #409EFF;
}
.head{
  height: 50px;
}
.box-card {
  width: 240px;
  height: 200px;
}
.button-style {
  margin-top: 24px;
}
</style>



